<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <!-- 头部搜索 -->
      <el-descriptions style="margin-bottom: 15px;">
        <el-descriptions-item label="物料编号">{{ baseInfo.materialCode || '---' }}</el-descriptions-item>
        <el-descriptions-item label="物料名称">{{ baseInfo.materialName || '---' }}</el-descriptions-item>
        <el-descriptions-item label="规格型号">{{ baseInfo.materialSpec || '---' }}</el-descriptions-item>
      </el-descriptions>
      <el-row>
        <table-com
          :ref="refName"
          :table-height-type="490"
          :loading="false"
          :index="false"
          :index-fixed="false"
          :selection="true"
          :multiple="multiple"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="baseInfo.inventoryList"
          :column-list="columnList"
          :show-operate="false"
          :operate-fixed="false"
          :table-selection="tableSelection"
          @selectionChange="selectionChange"
        />
      </el-row>
    </el-scrollbar>
    <package-to-s-k-u
      v-model="skuVisible"
      :title="'供应商条码'"
      :is-show-print-btn="false"
      :extra-info="{columnList: columnList1 || [], tableData: dialogInfo1.subBarCodeList || []}"
    />
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getDetailApi as getListSelectApi } from '@/apis/modules/out-bound/outbound-information-detail'
import PackageToSKU from '@/components/ModalSelect/PackageToSKU'

export default {
  name: 'MaterialSelect',
  components: {
    PackageToSKU
  },
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '选择退库内容'
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  dicts: [],
  data() {
    return {
      refName: 'modelSelectMaterialSelect',
      query: {
        supplierName: '',
        purchaseDate: '',
        materialsUnit: '',
        orderAmount: ''
      },
      columnList: [
        {
          module: 'word',
          prop: 'currentPosition',
          label: '库位',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'link',
          type: (item) => Array.isArray(item.subBarCodeList) && !!item.subBarCodeList.length ? 'primary' : '',
          prop: 'barcodeNumber',
          underline: false,
          minWidth: 150,
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleClick: (item) => {
            if (!item.subBarCodeList.length) return
            this.skuVisible = true
            this.dialogInfo1 = item
          }
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnList1: [
        {
          module: 'word',
          prop: 'currentPosition',
          label: '库位',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      baseInfo: {},
      skuVisible: false,
      dialogInfo1: {}
    }
  },
  methods: {
    confirmDialog() {
      if (!this.tableSelection.length) {
        this.$message({
          message: '请选择退货内容',
          type: 'warning'
        })
        return
      }
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    init() {
      return new Promise((resolve, reject) => {
        getListSelectApi({
          ...this.extraInfo
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseInfo = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    handleDefault() {
      this.tableSelection = new Array(0)
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.handleDefault()
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
